.homePage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #001164;
}

.homePage .row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    text-align: center;
}

.homePage h1,
.homePage h5,
.separator h3 {
    color: #fff;
}

.separator {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.garis_horizontal {
    border-left: 1px solid #fff;
    height: 170px;
    width: 170px;
    margin-left: 20px;
    margin-right: -100px;
}

.rounded-table {
    border: 4px solid black;
}

.nameLink {
    color: black;
    text-decoration: none;
}